<template>
    <div class="collect">
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <div class="header">我的收藏</div>
        <div class="collect-list">
            <div @click="goInfo(item.id)" class="collect-item" v-for="item in list" :key="item.id">
                <img :src="item.list_pic_url" alt="">
                <h4>{{item.name}}</h4>
                <p>￥{{item.retail_price}}</p>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        listAction
    } from '@/api/my/collect/index.js';
    export default {
        data() {
            return {
                list: [],
            };
        },
        computed: {

        },
        created() {

        },
        mounted() {
            listAction({
                openId: localStorage.getItem('openId')
            }).then(res => {
                this.list = res.data.collectGoodsList
            })
        },
        methods: {
            onClickLeft() {
                history.back();
            },
            goInfo(id) {
                this.$router.push(`/category/goods/info?id=${id}`)
            },
        },
    };
</script>

<style scoped lang="scss">
    .header {
        margin: 5px 0px;
        padding: 20px;
        background-color: #fff;
        text-align: center;
        font-weight: bold;
        font-size: 16px;
    }

    .collect-list {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .collect-item {
            width: 49%;
            background-color: #fff;
            margin-bottom: 5px;
            padding: 10px 0;
            img{
                display: block;
                margin: 0 auto;
                width: 166px;
                height: 166px;
            }
            h4{
                font-weight: normal;
                margin: 10px 10px;
            }
            p{
                margin: 5px 10px;
                color:#9c3232;
            }
        }
    }
</style>